import { topics } from '@/common/interface.js';
import { formateTime } from '@/common/day.js';
import articleList from '@/components/information/article-list.vue';
import loadMore from '@/components/uni-load-more/uni-load-more.vue';

export default {
	components: {
		articleList,
		loadMore
	},
	data() {
		return {
			tabBars: [
				{
					name: '全部',
					id: 'all'
				},
				{
					name: '精华',
					id: 'good'
				},
				{
					name: '分享',
					id: 'share'
				},
				{
					name: '问答',
					id: 'ask'
				},
				{
					name: '招聘',
					id: 'job'
				},
				{
					name: '测试',
					id: 'dev'
				}
			],
			currentData: {
				id: 'all',
				list: []
			},
			tabIndex: 0,
			page: 1,
			toView: 'all',
			scrollTop: 0,
			isFirst: true,
			loadingType: 0,
			loadingText: {
				contentdown: '上拉显示更多',
				contentrefresh: '正在加载...',
				contentnomore: '没有更多数据了'
			}
		};
	},
	onLoad() {
		this.loadData(0, 1);
	},
	onReachBottom() {
		this.loadData(this.tabIndex, this.page);
	},
	methods: {
		// tab切换触发的滑动效果和文章加载
		tabHandle(index) {
			uni.showLoading({
				title: '加载中'
			});
			this.tabIndex = index;
			switch (index) {
				case 0:
				case 1:
				case 2:
					this.toView = this.tabBars[0].id;
					break;
				default:
					this.toView = this.tabBars[3].id;
			}
			this.loadData(index);
		},
		// 将英文标题转为对应中文
		getLabel(tab) {
			let label = '';
			this.tabBars.forEach(item => {
				if (item.id === tab) {
					label = item.name;
				}
			});
			return label;
		},
		// 从API中获取数据
		loadData(index, page = 1) {
			let id = this.tabBars[index].id;
			topics({
				limit: 5,
				page: page,
				tab: id,
				mdrender: false
			}).then(res => {
				if (res.statusCode === 200) {
					let resData = res.data.data;
					let getData = [];
					getData = resData.map(item => {
						item.create_at = formateTime(item.create_at);
						item.label = this.getLabel(item.tab);
						return item;
					});

					if (this.tabBars[index].id === this.currentData.id) {
						if (this.isFirst) {
							this.isFirst = false;
							this.currentData.list = getData;
							this.page++;
						} else {
							this.currentData.list = this.currentData.list.concat(getData);
							this.page++;
						}
					} else {
						this.currentData.list = [];
						this.$nextTick(function() {
							this.currentData.list = getData;
							this.page = 2;
							this.currentData.id = this.tabBars[index].id;
							uni.pageScrollTo({
								scrollTop: 0,
								duration: 0
							});
						});
					}
				}
			});
			setTimeout(function() {
				uni.hideLoading();
			}, 500);
		}
	}
};
